<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-lowongan',
  data () {
    return {
      config: {
        title: 'Lowongan Pekerjaan',
        model_api: 'lowongan',
        getter: 'lowongan',
        setter: 'lowongan',
        pk_field: null,
        permission: {
          create: 'create-lowongan',
          read: 'view-lowongan',
          update: 'update-lowongan',
          delete: 'delete-lowongan'
        },
        slave: [
          {
            name: 'Pelamar',
            permission: 'lowongan-pelamar',
            module: 'ketenagakerjaan/lowongan-pelamar',
            as_param: 'lowongan_id',
            key_field: 'id'
          }
        ],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'lowongan_code', label: 'Kode Lowongan' },
          { id: 'lowongan_title', label: 'Judul Lowongan' },
          { id: 'position_count', label: 'Tenaga Dibutuhkan', methods: { list: { transform: 'suffix-orang' }, detail: { transform: 'suffix-orang' } } },
          {
            id: 'img_poster',
            label: 'Poster Lowongan',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'img_poster.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }],
                view_data: 'img_poster'
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              create: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              create: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



